import { Component, OnInit } from '@angular/core';
import { SideBarMenuService } from './side-bar-menu.service';

@Component({
  selector: 'app-side-bar-menu',
  templateUrl: './side-bar-menu.component.html',
  styleUrls: ['./side-bar-menu.component.css'],
  providers: [SideBarMenuService]
})
export class SideBarMenuComponent implements OnInit {
//  showStyle: false;
//class add/remove on click function
hightlightStatus: Array<boolean> = [];
hightlightStatus1: Array<boolean> = [];

  constructor( private sideMenuSvc:SideBarMenuService) { 
    this.getMenu();
  }

  //menu list data receive from json
  menus:any;
  menusLive:any;
  getMenu(){
    this.sideMenuSvc.getMenu()
    .subscribe(res=>{
      console.log(res.menusLive);
      this.menus = res.menus;
      this.menusLive = res.menusLive;
    })
  }




  ngOnInit() {
  }



}



